import * as React from 'react';
import { createRoot } from 'react-dom/client';
import { createInstance, Piral } from 'piral';
import { createLazyApi } from 'piral-lazy';
import { createCustomApi } from './api';
import { errorComponents, components } from './components';

const feedUrl = 'https://feed.piral.cloud/api/v1/pilet/<my-pilet-feed>';

function requestPilets() {
  return fetch(feedUrl)
    .then(res => res.json())
    .then(res => res.items);
}

const instance = createInstance({
  requestPilets,
  state: { errorComponents, components },
  plugins: [createCustomApi(), createLazyApi()],
});

const root = createRoot(document.querySelector('#app'));
root.render(<Piral instance={instance} />);